@import "tailwindcss/base";
@import "tailwindcss/components";
@import "tailwindcss/utilities";

/* ------ Add your CSS below this line (or @import your other CSS files). ------- */

@font-face {
    font-family: 'Vacation';
    src: url('../fonts/Vacation-Heavy.woff2') format('woff2'),
        url('../fonts/Vacation-Heavy.woff') format('woff');
    font-weight: 900;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'Vacation Slanted';
    src: url('../fonts/Vacation-Slanted.woff2') format('woff2'),
        url('../fonts/Vacation-Slanted.woff') format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'Vacation Deep';
    src: url('../fonts/Vacation-Deep.woff2') format('woff2'),
        url('../fonts/Vacation-Deep.woff') format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

*{
	font-family: 'Inter', sans-serif;
    font-size: 1.25rem;
    font-weight: 500;
}

h1, h2, h3{
    font-weight: 900;
}

.noto-emoji{
    font-family: "Noto Emoji";
    font-weight: bold;
}

.day p{
    @apply bg-gray-100 p-2 px-3 mb-2 rounded-lg leading-tight dark:bg-white/10;
}
.day p span{
    @apply text-base opacity-50;
}

